  align-items: center;
  padding: 14px 17px; }

.footer__copyright-text,
.footer__copyright-link {
  margin: 0;
  text-transform: uppercase;
  color: #666666;
  opacity: 0.5;
  font-size: 13px;
  line-height: 24px;
  letter-spacing: 0.04em; }
  .footer__copyright-text:hover, .footer__copyright-text:focus,
  .footer__copyright-link:hover,
  .footer__copyright-link:focus {
    opacity: 1; }

.footer__copyright-text:first-of-type {
  margin-right: 75px; }

.footer__copyright-text:last-of-type {
  margin-left: auto; }

.footer__copyright-text--confidential {
  margin-left: 142px; }

@media (max-width: 1023px) {
  .footer__container {
    padding: 15px 0 25px 0; }
  .footer__main-content {
    flex-wrap: wrap; }
  .copyright-text--desktop {
    display: none; }
  .footer__copyright-text:first-of-type {
    display: none; }
  .footer__copyright-text--confidential {
    margin-left: 0; } }

@media (max-width: 767px) {
  .footer {
    overflow: hidden; }
  .footer__copyright {
    border: none; }
  .footer__wrapper {
    flex-direction: column;
    align-items: center; }
  .footer__copyright-text {
    margin-bottom: 20px; }
    .footer__copyright-text:last-of-type {
      margin: 0; } }

.social {
  margin-right: 145px; }

.social__list {
  margin: 0;
  padding-left: 0;
  list-style: none;
  display: flex; }

.social__logo {
  margin-bottom: 50px; }
  .social__logo:hover, .social__logo:focus {
    opacity: 0.5; }

.social__item {
  margin-right: 16px; }

.social__link:hover, .social__link:focus {
  opacity: 0.6; }

.social__link:active {
  opacity: 0.5; }

.copyright-text {
  display: none; }

@media (max-width: 1023px) {
  .social {
    margin-right: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid rgba(98, 105, 98, 0.2);
    width: 100%; }
  .social__logo {
    margin-bottom: 0;
    padding: 0 25px 25px; }
  .social__item {
    margin-right: 14px; }
  .copyright-text {
    display: block;
    margin: 0;
    letter-spacing: 0.04em;
    font-size: 13px;
    line-height: 24px; } }

@media (max-width: 767px) {
  .social {
    flex-direction: column;
    padding: 32px 0 51px 0; }
  .social__logo {
    padding: 0;
    margin-bottom: 20px; }
  .copyright-text {
    margin-bottom: 32px; } }

.accordion {
  position: relative; }

.accordion h3 {
  margin: 0 0 25px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 15px;
  font-weight: normal;
  line-height: 16px;
  text-transform: uppercase;
  letter-spacing: 0.05em; }

.accordion__btn {
  display: none; }

@media (max-width: 767px) {
  .accordion__btn {
    display: block;
    position: absolute;
    top: 22px;
    right: 18px;
    width: 30px;
    height: 30px;
    background-image: url("../img/icon-plus.svg");
    background-repeat: no-repeat;
    background-color: transparent;
    border: none;
    outline: none; }
    .accordion__btn:hover, .accordion__btnfocus {
      opacity: 0.6; }
  .accordion__btn--opened {
    background-image: url("../img/icon-minus.svg");
    background-repeat: no-repeat; } }

.nav__list {
  margin: 0;
  padding-left: 0;
  list-style: none;
  column-count: 2; }
  .nav__list--contacts {
    column-count: 1; }

.nav__list li {
  margin-right: 60px; }

.nav__list--contacts > li {
  margin-right: 0; }

.nav__item,
.nav__link {
  color: rgba(98, 105, 98, 0.5);
  font-size: 15px;
  line-height: 26px; }
  .nav__item:hover, .nav__item:focus,
  .nav__link:hover,
  .nav__link:focus {
    color: #1b2d37; }

@media (max-width: 1023px) {
  .nav {
    padding: 35px 0 8px 25px; }
    .nav--contacts {
      padding: 35px 25px 8px 0; }
  .nav__list {
    column-count: auto; }
  .nav__list li {
    margin-right: 0; } }

@media (max-width: 767px) {
  .nav {
    width: 100%;
    padding-top: 25px;
    padding-left: 30px;
    padding-right: 15px;
    border: 1px solid rgba(98, 105, 98, 0.2); }
  .nav__list {
    display: none;
    padding-top: 6px;
    padding-bottom: 27px; }
  .nav__list--opened {
    display: block; } }
